html,
body {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    color: #000;
    overflow: hidden;
    [contenteditable="true"]:focus {
        outline: none;
    }
    input:focus,
    select:focus,
    textarea:focus,
    button:focus {
        outline: none;
    }
}